import React, { Component } from 'react'
import web from "../src/images/img-header2.png";
import Common from './Common'
import capture from "../src/images/Capture.PNG";
import capture2 from "../src/images/Capture2.PNG";



function Home(){

    $(document).ready(function () {

        $(".filter-button").click(function () {
          var value = $(this).attr('data-filter');
    
          if (value == "all") {
            $('.filter').show('1000');
          }
          else {
            $(".filter").not('.' + value).hide('3000');
            $('.filter').filter('.' + value).show('3000');
    
          }
        });
    
        if ($(".filter-button").removeClass("active")) {
          $(this).removeClass("active");
        }
        $(this).addClass("active");
    
      });

      

        return (
            <div classNameName="div">
                <div className="home-page">
                    <Common
                        name='Grow your business with '
                        description="We are making websites for everyone around the world"
                        imgsrc={web}
                        visit="/services"
                        btname="Get Started"
                    />
                </div>
                <div className="bg-white py-5">
                    <div className="container py-5">
                        <h1 className="text-center mb-5 top-text">*What We Do*</h1>
                        <div className="row align-items-center mb-5">
                            <div className="col-lg-6 order-2 order-lg-1">
                                <h2 className="font-weight-light">MOBILE FRIENDLY</h2>
                                <p className="font-italic text-muted mb-4">We making websites for all devices. Responsive Web design is the approach that suggests that design and development should respond to the user’s behavior and environment based on screen size, platform and orientation.</p><a href="#" className="btn btn-dark px-5 rounded-pill shadow-sm">Get Examples</a>
                            </div>
                            <div className="col-lg-5 px-5 mx-auto order-1 order-lg-2"><img src="https://res.cloudinary.com/mhmd/image/upload/v1556834139/img-1_e25nvh.jpg" alt="" className="img-fluid mb-4 mb-lg-0" /></div>
                        </div>
                        <div className="row align-items-center">
                            <div className="col-lg-5 px-5 mx-auto"><img src="https://res.cloudinary.com/mhmd/image/upload/v1556834136/img-2_vdgqgn.jpg" alt="" className="img-fluid mb-4 mb-lg-0" /></div>
                            <div className="col-lg-6">
                                <h2 className="font-weight-light">Great Design</h2>
                                <p className="font-italic text-muted mb-4">Isn’t it amazing how easy it is to purchase stuff without even leaving the house? From groceries to jewelry, clothes, and other household items, online shopping has become more of a norm than a novelty. To capture your share of this trend, you will need to create an effective online store, one with great web design.</p><a href="#" className="btn btn-dark px-5 rounded-pill shadow-sm">Get Examples</a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="container mt-5">
                    <div class="row">
                        <div class="gallery col-lg-12 col-md-12 col-sm-12 col-xs-12">
                            <h1 class="gallery-title">Our Website Examples</h1>
                        </div>
                        <div align="center">
                            <button class="btn btn-dark filter-button" data-filter="all">All</button>
                            <button class="btn btn-dark filter-button" data-filter="hdpe">Personal</button>
                            <button class="btn btn-dark filter-button" data-filter="sprinkle">Portfolio</button>
                            <button class="btn btn-dark filter-button" data-filter="spray">E-commerce</button>
                            <button class="btn btn-dark filter-button" data-filter="irrigation">Entertainment</button>
                        </div>
                        <br />



                        <div class="card gallery_product col-lg-4 col-md-4 col-sm-4 col-xs-6 filter hdpe">
                            <img src={capture} class="img-responsive" />
                        </div>

                        <div class="card gallery_product col-lg-4 col-md-4 col-sm-4 col-xs-6 filter sprinkle">
                            <img src={capture2} class="img-responsive" />
                        </div>

                        <div class="card gallery_product col-lg-4 col-md-4 col-sm-4 col-xs-6 filter hdpe">
                            <img src={capture} class="img-responsive" />
                        </div>

                        <div class="card gallery_product col-lg-4 col-md-4 col-sm-4 col-xs-6 filter irrigation">
                            <img src={capture2} class="img-responsive" />
                        </div>

                        <div class="card gallery_product col-lg-4 col-md-4 col-sm-4 col-xs-6 filter spray">
                            <img src={capture} class="img-responsive" />
                        </div>

                        <div class="card gallery_product col-lg-4 col-md-4 col-sm-4 col-xs-6 filter irrigation">
                            <img src={capture2} class="img-responsive" />
                        </div>

                    </div>
                </div>
            </div>
        )
}

export default Home;